import { withPrefix } from 'gatsby-link';

const getContainingDir = path => {
  const pathArr = path.split(/[\\/]/);
  return pathArr[pathArr.length - 2];
};

const getStringArrayFromPath = path => {
  const orderedCatStr = getContainingDir(path);
  if (typeof orderedCatStr !== 'string') {
    return new Array('');
  }
  const re = /^(\d+[-_])(.*)/;
  if (!re.test(orderedCatStr)) {
    return new Array(orderedCatStr);
  }
  return orderedCatStr.match(re);
};

const removeOrderingInfo = str => {
  const matches = getStringArrayFromPath(str);
  const unordered = matches ? matches[matches.length - 1] : '';
  return typeof unordered === 'string' ? unordered : str;
};

export const getCategoryFromPath = path => {
  const catString = removeOrderingInfo(path);
  const splitRe = /[-_]/;
  //return a formatted category string, also replacing the full word 'and' with an ampersand
  return catString.split(splitRe).join(' ').replace('and', '&');
};

export const getOrderFromPath = path => {
  const matches = getStringArrayFromPath(path);
  var digitsRe = /\d+/;
  //give up and return string representation of first order if can't match order info from the containing directory
  return matches && matches.length === 3 ? matches[1].match(digitsRe)[0] : '00';
};

export const getPageSpecificClass = location => {
  let pageClass;
  const rootPath = `/`;
  const prefixedRootPath = withPrefix(rootPath);
  const pathHasPrefix = rootPath !== prefixedRootPath;
  const modPathName = pathHasPrefix
    ? location.pathname.substring(prefixedRootPath.length)
    : location.pathname.substring(1); // pathname minus leading slash
  const trailingSlash = modPathName.indexOf('/') > -1; // boolean, path has trailing slash

  pageClass =
    location.pathname === rootPath || location.pathname === prefixedRootPath
      ? 'home'
      : trailingSlash
      ? modPathName.substring(0, modPathName.indexOf('/'))
      : modPathName;

  return pageClass;
};
