import React from 'react';
import Link, { withPrefix } from 'gatsby-link';
import Nav from './Nav';
import '../../static/main.scss';
import { getPageSpecificClass } from '../utils/getDataFromPath';
import { graphql, StaticQuery } from 'gatsby';

import { rhythm } from '../utils/typography';

const Layout = ({ children, location, footer }) => (
  <StaticQuery
    // Return markdown title and navTitle for pages only, and not the root index page, along with page paths
    // Working exclusion regex : regex: "/^(?!.*(\/(posts|partials)))/",
    // but with reorgz, should be able to do inclusion of pages only
    query={graphql`
      query LayoutQuery {
        pagesMarkdown: allMarkdownRemark(
          filter: { fileAbsolutePath: { regex: "//pages/", ne: "/" } }
        ) {
          edges {
            node {
              fileAbsolutePath
              frontmatter {
                title
                navTitle
              }
            }
          }
        }
        pages: allSitePage {
          edges {
            node {
              path
            }
          }
        }
        site {
          siteMetadata {
            title
          }
        }
      }
    `}
    render={data => {
      // console.log('In layout component render')
      // console.log(location)
      // console.log(children)
      // console.log(data)
      let header;
      let rootPath = `/`;
      if (!data) {
        return <p>Nothing here!</p>;
      }
      const navPaths = data.pages.edges.map(edge => edge.node.path);
      //console.log('navPaths', navPaths)
      const navData = data.pagesMarkdown.edges.map(edge => {
        const path = navPaths.filter(
          navPath =>
            edge.node.fileAbsolutePath.indexOf(navPath) !== -1 &&
            navPath !== '/'
        )[0];
        const title = edge.node.frontmatter.navTitle
          ? edge.node.frontmatter.navTitle
          : edge.node.frontmatter.title;
        return {
          title,
          path,
        };
      });

      const pageLayoutClass = getPageSpecificClass(location);

      if (withPrefix) {
        rootPath = withPrefix(rootPath);
      }
      let isHome = false;
      let typistIncompleteClass = 'typist-incomplete';

      isHome = location.pathname === rootPath;

      if (isHome) {
        header = (
          <header className="header header_home">
            <Nav items={navData} classNames="header__nav" />
          </header>
        );
      } else {
        typistIncompleteClass = '';
        header = (
          <header className="header header_page">
            <h3
              style={{
                fontFamily: 'Montserrat, sans-serif',
                marginTop: 0,
                marginBottom: rhythm(-1),
              }}
            >
              <Link
                style={{
                  boxShadow: 'none',
                  textDecoration: 'none',
                  color: 'inherit',
                }}
                to={'/'}
              >
                {data.site.siteMetadata.title}
              </Link>
            </h3>
            <Nav items={navData} classNames="header__nav" />
          </header>
        );
      }
      return (
        <div
          role="presentation"
          className={`layout main layout_${pageLayoutClass} ${typistIncompleteClass}`}
          style={{
            maxWidth: rhythm(24),
            minWidth: rhythm(14),
            padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
          }}
        >
          {header}
          <main className="main">
            {children}
          </main>
          <footer className="footer">
            {footer}
          </footer>
        </div>
      );
    }}
  />
);

export default Layout;
