import React from 'react';
import Link from 'gatsby-link';

const Nav = props => {
  // console.log('props in Nav', props);
  // console.log('props items in Nav', props.items);
  const navClasses = `nav${props.classNames ? ' ' + props.classNames : ''}`;
  // console.log(`props.classNames: ${props.classNames}`)
  return (
    <nav style={props.style} className={`typist-hidden ${navClasses}`}>
      <ul className="nav__list">
        {props.items.sort(navItemSortFn).map(navItem => (
          <li className="nav__item" key={navItem.path}>
            <Link className="nav__link" to={navItem.path}>
              {navItem.title}
            </Link>
          </li>
        ))}
      </ul>
    </nav>
  );
};

const navItemSortFn = (itemA, itemB) =>
  itemA.title < itemB.title ? -1 : itemB.title < itemA.title ? 1 : 0;

export default Nav;
